.modal {
  position: absolute;
  z-index: 200;
  top: 50%;
  left: 50%;
  transform: perspective(0) translate(-50%, -50%) rotateX(0deg);
  padding: 25px;
  padding-top: 50px;
  overflow: hidden;

  max-width: 80%;
  max-height: 80%;

  @media screen and (min-width: 768px) {
    max-width: 560px;
  }

  @media screen and (max-width: 1279px) {
    overflow: scroll;
  }

  background: linear-gradient(to bottom, var(--btn-bg-color) 40%, var(--primary-bg-color));

  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 30px;

  transition: transform 800ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal__title {
  width: 100%;
  font-family: var(--secondary-font);
  @include font(20px, 400, 23px, normal);
  color: var(--primary-title-color);
  text-align: center;
  display: inline-block;

  @media screen and (min-width: 768px) {
    @include font(34px, 400, 39px, normal);
  }
}

.modal__text {
  margin-top: 30px;
  font-family: var(--primary-font);
  @include font(12px, 500, 19.44px, normal);
  color: var(--secondary-text-color);
  text-align: center;

  @media screen and (min-width: 768px) {
    @include font(16px, 400, 28px, normal);
  }
}

